@import '../../../assets/sass/main.scss';

.pagination_elements {
  display: flex;
  justify-content: center;
  gap: 6px;
  margin-top: 60px;
}

.pagination_element {
  @include font-styles(14px, 400, 20.02px, 0.17px);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid $light-dark-stroke;
  color: $dark-text;
  background-color: transparent;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

  &_active {
    border: 1px solid $dark-text;
  }

  &:hover,
  &:focus {
    color: $gray-text;
    background-color: $dark-text;
  }
}
